import React from 'react';
import {Link} from 'react-router-dom';
import {Stack} from '@mui/material';
import s from './Navbar.scss'
import Logo from '../../assets/images/Logo.png';


const Navbar = () => (
    <div className={'NavBar'} style={s}
           sx={{gap: {sm: '123px', xs: '40px'}, mt: {sm: '32px', xs: '20px'}, justifyContent: 'none'}} px="20px">
        <Link to="/">
            <img src={Logo} alt="logo" style={{width: '48px', height: '48px', margin: '0px 20px'}}/>
        </Link>
        <Stack
            direction="row"
            gap="40px"
            fontFamily="Alegreya"
            fontSize="24px"
            alignItems="flex-end"
        >
            <Link to="/"
                  style={{textDecoration: 'none', color: '#3A1212', borderBottom: '3px solid #FF2625'}}>Home</Link>
            <Link to="/exercises"
                  style={{textDecoration: 'none', color: '#3A1212', borderBottom: '3px solid #FF2625'}}>Exercises</Link>

            <Link to="/recepies"
                  style={{textDecoration: 'none', color: '#3A1212', borderBottom: '3px solid #FF2625'}}>Recipes</Link>
        </Stack>

    </div>
);

export default Navbar;
